import Modal from 'react-bootstrap/Modal';
import React,{useEffect, useState} from "react";
import { aanmaken_klant } from "../../actions/project";
import { connect } from 'react-redux'
import AlertModal from '../messages/AlertModal';
// import useTimeout from "@use-it/timeout";
// import Alert from '@mui/material/Alert';
// import MuiAlert from '@mui/material/Alert';

const KlantModalAanmaken = (props) => {
    const klant = {
        klant_naam: '',
        plaatsklant: '',
        landklant: '',
        provincieKlant: '',
        phoneklant: '',
    }
   
    const validationsMessages = "colorvalidations"
    const [pronvinciesKlant,setPronvinciesKlant] = useState([])
    const [error,setError] = useState(false)
    const [submited_success,setSubmited_success] = useState(false)
    const [message,setMessage] = useState(null)
    const [severity,setSeverity] = useState(null)
    const [klantForm,setKlantform] = useState(klant)
    const {klant_naam,plaatsklant,landklant,provincieKlant,phoneklant} = klantForm
    const onChangeKlant = e => setKlantform({...klantForm, [e.target.name]: e.target.value})

    useEffect(() => {
        if(props.message)
        {
            setMessage(props.message)
        }
    },[props.message])
    const choosedCountry = (e) => {
        if(props.props.provincies !== undefined)
        {
            if(e.target.value === "nl"){
                klantForm.landklant = 'Nederland'
                setPronvinciesKlant(props.props.provincies.nl)
            }else if(e.target.value === "en"){
                klantForm.landklant = 'England'
                setPronvinciesKlant(props.props.provincies.en)
            }else{
                setPronvinciesKlant([])
            }
        }
    }
  
    const submitKlant = (e) => {
        e.preventDefault()
        if(klantForm.klant_naam.trim() && klantForm.landklant.trim() && klantForm.phoneklant.trim() && klantForm.provincieKlant
            && klantForm.plaatsklant)
            {
               
                if (props.aanmaken_klant(JSON.stringify(klantForm))){
                   
                    setError(false)
                    setSubmited_success(true)
                    setTimeout(() => { 
                        setSubmited_success(false)
                        props.props.setShow(false)   
                    }, 2000) 
                 }
            }else{
           
                setError(true)
            }     
    }
    // const return_alert_message = () => {
    //     if(submited_success === true)
    //     {
    //         setTimeout(() => {
    //             return <AlertModal props={{'severity': 'success','message':'success created!'}} />
    //         },[2000])
    //         // setSubmited_success(false)
    //     }else{
    //         return null
    //     }
        
    // }
    return (<>
    <Modal
        show={props.props.show}
        onHide={() => props.props.setShow(false)}
        animation={true}
        centered={true}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
        >
        <Modal.Header closeButton>
        <Modal.Title id="example-custom-modal-styling-title">
            Klant aanmaken
            {submited_success ? <AlertModal props={{'severity': 'success','message': props.message ? props.message:'geen melding'}} />:null}
        </Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <div className='card card-animate'>
            <form action="#">
                <div className="row m-3">
                    <div className="col-lg-3">
                        <label for="nameInput" className="form-label">Naam</label>
                    </div>
                    <div className="col-lg-9">
                        <input type="text" name="klant_naam" value={klant_naam} onChange={(e) => onChangeKlant(e)} className="form-control form-control-sm" id="nameInput" placeholder="Voorbeeld: De vree en sliepen " />
                        {error === true && !klantForm.klant_naam.trim() ? <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                    </div>
                </div>
                <div className="row m-3">
                    <div className="col-lg-3">
                        <label for="plaatsklant" className="form-label">Plaats</label>
                    </div>
                    <div class="col-lg-9">
                        <input type="text" name="plaatsklant" value={plaatsklant} onChange={(e) => onChangeKlant(e)} class="form-control form-control-sm" id="plaatsklant" placeholder="Voorbeeld: Amsterdaam" />
                        {error === true && !klantForm.plaatsklant.trim() ? <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                    </div>
                </div>
                <div className='row m-3'>
                    <div className="col-lg-3">
                        <label for="land" class="form-label">Land</label>
                    </div>
                    <div class="col-lg-9">
                        <select 
                            id="landklant" 
                            name="landKlant"
                            defaultValue={landklant}
                            className="form-select form-select-sm"
                            onChange={(e) => choosedCountry(e)}
                        >
                            <option> </option>
                            <option value="nl">Nederland</option>
                            <option value="en">England</option> 
                        </select>
                        {error === true && !klantForm.landklant.trim() ? <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                    </div>
                </div>
                <div className='row m-3'>
                    <div class="col-lg-3">
                        <label for="provincieKlant" class="form-label">Provincie</label>
                    </div>
                    <div class="col-lg-9">
                        <select 
                            id="provincieKlant"
                            name="provincieKlant" 
                            defaultValue={provincieKlant}
                            className="form-select form-select-sm"
                            onChange={(e) => onChangeKlant(e)}
                        >
                            <option></option>
                            {
                                pronvinciesKlant !== undefined ? pronvinciesKlant.map((item) => <option key={item} value={item}>{item}</option>): <option> - No country is choosen</option>
                            }
                            
                        </select>
                        {error === true && !klantForm.provincieKlant.trim() ? <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                    </div>
                </div>

                <div class="row m-3">
                    <div class="col-lg-3">
                        <label for="contactNumber" class="form-label">Phone</label>
                    </div>
                    <div class="col-lg-9">
                        <input type="number" name='phoneklant' value={phoneklant} onChange={(e) => onChangeKlant(e)}  class="form-control form-control-sm" id="contactNumber" placeholder="+31 698478321" />
                        {error === true && !klantForm.phoneklant.trim() ? <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                    </div>
                </div>
                <div class="text-end">
                    <div class="row m-3">
                        <div className='col'>
                            <button type="button" onClick={(e) => submitKlant(e)} className="btn btn-light btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                        </div>
                        <div className='col'>
                            <button type="button" onClick={() => props.props.setShow(false)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i>Cancel</button>
                        </div>
                    </div>    
                </div>
            </form>
        </div>
        </Modal.Body>
    </Modal>
    
    </>)

}
const getDatas = state => ({
    message: state.project.is_success,
})

export default connect(getDatas,{aanmaken_klant})(KlantModalAanmaken)