import React, { useEffect, useMemo, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import AlertModal from '../../components/messages/AlertModal';
// REDUX
import {load_project,aanmaken_klantMedewerker,aanmaken_klant,
    aanmaken_project,bedrijf_aanemer_bedrijf_aanmaken} from '../../actions/project'
import {medewerker_aanmaken }from '../../actions/medewerker'
import { connect } from 'react-redux'
import MedewerkerModal from '../../components/admin/MedewerkerModal';
import KlantMedewerkerModal from '../../components/project/KlantMedewerkerModal';
import MessagingModal from '../../components/messages/MessagingModal';
import KlantModalAanmaken from '../../components/project/KlantModalAanmaken';
import BedrijfAanemerModal from '../../components/project/BedrijfAanemerModal';
// Toastyfy
const ProjectAanmaken = (props) => {
    
    const project = {
        projectnr: props.context !== undefined ? props.context.projectnr: '',
        projectnaam: '',
        plaats: '',
        provincie: '',
        land:'',
        selectedKlanten: '',
        projectStatus: '',
        offertenr: null,
        exactnr: null,
        debiteurnr: null,
        renovatie_nieuwbouw_: '',
        selectedProjecleiderAanermer: null,
        selectedwerkvoorbereiderAanemer: null,
        selectedUivoerderAanemer: null,
        selectedWerkvoorbereiderFz: null,
        selectedProjecleiderFz: null,
        inopdrachtvoor_vloerverwarming: null,
        onderaannemers_vloerverwarming: null,
        ordernr_onderaannemer_vloerverwarming: null,
        inopdrachtvoor_ventilatieinstallatie: null,
        onderaannemers_ventilatieinstallatie: null,
        ordernr_onderaannemer_ventilatieinstallatie: null,
        inopdrachtvoor_: null,
        onderaannemers_: null,
        ordernr_onderaannemer: null,
        system_datum_invoer: null,
        startDatum: null,
        offerteDatum: null,
        uitlijk_datum_opdracht_indien_wtw: null,
        uitlijk_datum_opdracht_alleen_icem: null,
        opmerking:'',
    }
    const [message,setMessage] = useState(null)
    const [formData,setFormdata] = useState(project)
    const [pronvincies,setPronvincies] = useState()
    const [projectleidersAanemers,setProjectleidersAanmers] = useState([])
    const [werkvoorbereidersAanemers,setWerkvoorbereidersAanmers] = useState([])
    const [uitvoerdersAanemers,setUitvoerdersAanmers] = useState([])
    const [projectleiderfz,setProjectleiderFz] = useState([])
    const [werkvoorbereiderfz,setWerkvoorbereiderFz] = useState([])
    const [klanten, setKlanten] = useState()
    const projectstatus = ['Lead','Offerte','Onderhandeling','In uitvoering','In service','Vervallen']
    const [show, setShow] = useState(false);
    const [showOnderaanemerModal, setShowOnderaanemerModal] = useState(false);
    const [showModalAanemer, setShowModalAanemer] = useState(false);
    const [showModalFzMedewerker,setShowModalFzMedewerker] = useState(false)
    const [showModalMessage,setShowModalMessage] = useState(false)
    const [submited_success,setSubmited_success] = useState(null)
    const [subimt_klant_button,setSubimt_klant_button] = useState(null)
    const [error, setError] = useState(false);
    const [bedrijfAanemer,setBedrijfAanemer] = useState([])
    const [pronvincieS,setPronvincieS] = useState([])
    const [submit,setSubmit] = useState(false)
    const validationsMessages = "colorvalidations"
    const severity = useRef('warning')
    const navigate = useNavigate()
    useEffect(() => {
        props.load_project()
    },[submited_success,showModalFzMedewerker])

    
    useEffect(() => {
        
        if(props.context !== undefined)
        {
            setKlanten(props.context.klant)
            setProjectleidersAanmers(props.context.projectleidersAanemers)
            setWerkvoorbereidersAanmers(props.context.werkvoorbereidersAanemers)
            setUitvoerdersAanmers(props.context.uitvoerders)
            setProjectleiderFz(props.context.projectleiders)
            setWerkvoorbereiderFz(props.context.werkvoorbereiders)
            setPronvincieS(props.context.provincies)
        }
    },[props.context,show,showModalAanemer,showOnderaanemerModal,showModalFzMedewerker])

    useEffect(() => {
        if(props.message){
            setMessage(props.message)
        }
    },[message])
    
    useEffect(() => {
        if(props.bedrijaanemer !== undefined)
        {
            setBedrijfAanemer(props.bedrijaanemer)
        }
    },[props.bedrijaanemer])

    
    const submitCancel = (e) => {
        e.preventDefault()
        navigate('/projectenlist')
    }
    
    const choosedCountry = (e) => {
        e.preventDefault()
        if(props.context !== undefined)
        { 
            
            if(e.target.value === "nl")
            {
                formData.land = 'Nederland' 
                setPronvincies(props.context.provincies.nl)

            }else if(e.target.value === "en")
            {
                formData.land = 'England' 
                setPronvincies(props.context.provincies.en)       
            }
            else{
                setPronvincies([])
            }  
        }
    }
    const currentDate = () => {
        const today = new Date();
        // const yyyy = today.getFullYear();
        // let mm = today.getMonth() + 1; // Months start at 0!
        // let dd = today.getDate();

        // if (dd < 10) dd = '0' + dd;
        // if (mm < 10) mm = '0' + mm;

        // const formattedToday = dd + '/' + mm + '/' + yyyy;
        return today
    }
    function padTo2Digits(num) {
        return num.toString().padStart(2, '0');
      }
    const returnDateFormat = (date) => {
        const new_date = new Date(date)
        if(new_date !== '')
        {
            return [
                padTo2Digits(new_date.getDate()),
                padTo2Digits(new_date.getMonth() + 1),
                new_date.getFullYear(),
              ].join('-');

        }else{
            return new_date
        }
        
    }
    const {projectnaam,plaats,offertenr,exactnr,selectedKlanten,projectStatus,
        debiteurnr,renovatie_nieuwbouw_,selectedProjecleiderAanermer,selectedwerkvoorbereiderAanemer,selectedUivoerderAanemer,selectedProjecleiderFz,selectedWerkvoorbereiderFz,
        inopdrachtvoor_vloerverwarming,onderaannemers_vloerverwarming,ordernr_onderaannemer_vloerverwarming,inopdrachtvoor_ventilatieinstallatie,
        onderaannemers_ventilatieinstallatie,ordernr_onderaannemer_ventilatieinstallatie,inopdrachtvoor_,onderaannemers_,ordernr_onderaannemer,
        opmerking,startDatum,offerteDatum,uitlijk_datum_opdracht_indien_wtw,uitlijk_datum_opdracht_alleen_icem,
        } = formData
    const onChangeData = e => setFormdata({...formData, [e.target.name]: e.target.value})

    const submitForm = (e) => {
        e.preventDefault()
        formData.projectnr = project.projectnr
        formData.system_datum_invoer = returnDateFormat(currentDate())
        formData.startDatum = returnDateFormat(formData.startDatum)
        formData.offerteDatum = returnDateFormat(formData.offerteDatum)
        formData.uitlijk_datum_opdracht_indien_wtw = returnDateFormat(formData.uitlijk_datum_opdracht_indien_wtw)
        formData.uitlijk_datum_opdracht_alleen_icem = returnDateFormat(formData.uitlijk_datum_opdracht_alleen_icem)
        if(formData.projectnaam === '' || formData.plaats === '' ||
        formData.provincie === '' || formData.land === '' || formData.selectedKlanten === '' ||
        formData.projectStatus === '' || formData.renovatie_nieuwbouw_ === ''
        )
        {
            // setShowModalMessage(true)
            // setSubmit(true)
            setMessage('Kijk boven wat er aan hand met invullen van de form')
            severity.current = 'error'
            setSubmited_success(true)
            setError(true)
            setShowModalMessage(true)
            //settimeout

        }else{
            setSubmited_success(true)
            if(props.aanmaken_project(JSON.stringify(formData))){
                setMessage('Project successvol toegevoegd')
                severity.current = 'success'
                setShowModalMessage(true)
                setTimeout(() => {
                    setMessage(null)
                    setShowModalMessage(false)
                    setError(false)
                    setSubmited_success(false)
                    setFormdata(project)
                    navigate('/projectenlist')
                }, 2000);
             }
            
        }
        
        
    }
    const returnArrayKlantMedewerker = (arrayobjects,e) => {
        var itemKlantmedewerker = []
        arrayobjects.forEach(element => {if(element.klantID === parseInt(e)){  itemKlantmedewerker.push({'id':element.id,'name':element.voornaam +' '+element.achternaam})}})
        return itemKlantmedewerker
    }
    const [projectleiderAanemersValues,setprojectleiderAanemersValues] = useState([])
    const [werkVoorbereidersAanemersValues,setWerkVoorbereidersAanmersValues] = useState([])
    const [uitvoerdersAanemersValues,setUitvoerdersAanmersValues] = useState([])

    const returnGegevensPerklant = (e) => {
        formData.selectedKlanten = e
        var projectleidersAanemers_ =  returnArrayKlantMedewerker(projectleidersAanemers !== undefined ? projectleidersAanemers:[],e)
        setprojectleiderAanemersValues(projectleidersAanemers_)

        var werkvooerbereiderAanemers_ =  returnArrayKlantMedewerker(werkvoorbereidersAanemers,e)
        setWerkVoorbereidersAanmersValues(werkvooerbereiderAanemers_)

        var uitvoerderAanemers_ =  returnArrayKlantMedewerker(uitvoerdersAanemers,e)
        setUitvoerdersAanmersValues(uitvoerderAanemers_)
       
        
    }

    var klantenArray = []
    if(klanten !== undefined)
    {
        for(var i in klanten)
        {
            klantenArray.push({'id':klanten[i].id,'naam':klanten[i].klantnaam})
        }

    }
  
    const displayModalBedrijfaannemer = () => {
        return (<>
        <BedrijfAanemerModal props={{ 'showOnderaanemerModal':showOnderaanemerModal, 'setShowOnderaanemerModal':setShowOnderaanemerModal }} />
        </>)
    }
    const handleModalklantMedewerker = () => {
       
        if(selectedKlanten !== undefined && selectedKlanten !== null && selectedKlanten !== '')
        {
            setSubimt_klant_button(true)
            setShowModalAanemer(true)
            setShowModalMessage(false)
        }else{
            setSubimt_klant_button(false)
            setShowModalMessage(true)
            setShowModalAanemer(false)
        }
    }
    const displayModalMessages = () => {

        if(submited_success === true)
        {
            return <MessagingModal props={{'showModalMessage':showModalMessage, 'setShowModalMessage':setShowModalMessage,'message': message,'severity': severity.current}} />
        }else
        {
            return null
        }
    }
    const displayModalAanemer = () => {

        if(subimt_klant_button === true )
      
        {
            // setMessage('')
            return <KlantMedewerkerModal props={{'showModalAanemer':showModalAanemer, 'setShowModalAanemer':setShowModalAanemer,'klantId': selectedKlanten ? selectedKlanten:null,}}/>
        }
       else{
            return <MessagingModal props={{'showModalMessage':showModalMessage, 'setShowModalMessage':setShowModalMessage,'message': 'Kiest eerste opdrachtgever','severity': severity.current}} />
        }  
    }

    const returnNumberPhone = (data,id) => {
        var phoneNumber 
        for(var i in data){
            if(data[i].id === parseInt(id)){
                phoneNumber = '+' +  data[i].phone_no
            }
        }
        return phoneNumber
    
    }

    const renderInputsInopdrachtvoorFZVloerverwarming = (orderaanemer,ordernr_onderaanermer_,idSelected,idSelectInput) => {
         
        
        return(<>
            <div className='row m-3'>
                <div class="col-lg-6">
                    <select 
                        id={idSelected}
                        name={idSelected}
                        defaultValue={orderaanemer}
                        className="form-select form-select-sm"
                        onChange={(e) => onChangeData(e)}
                    >
                        <option></option>
                       {bedrijfAanemer !== undefined ? bedrijfAanemer.map((e) => <option value={e[0]}>{e[1]}</option>): ''}
                        
                    </select><br></br>
                    <div class="col-lg-12">
                        <input className="form-control form-control-sm" name={idSelectInput} value={ordernr_onderaanermer_} type="text" onChange={e => onChangeData(e)}  floatlabeltype="Always" placeholder="Odernr." required={true}/>
                    </div>
                </div>
                <div className="col-lg-3">
                    <button type="button" title='Bedrijf aanemer toevogen' onClick={() => setShowOnderaanemerModal(true)} className="btn btn-success btn-icon waves-effect waves-light sm"><i className="ri-add-fill"></i></button>
                    {displayModalBedrijfaannemer()}
                </div>
                

            </div>
        </>)

    }
    const renderInstallatieComponenten = (inopdrachtvoor,idSelected) => {
        
        
        if(inopdrachtvoor === "FZ"){
            if(idSelected === "inopdrachtvoor_vloerverwarming")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming(onderaannemers_vloerverwarming,ordernr_onderaannemer_vloerverwarming,"onderaannemers_vloerverwarming","ordernr_onderaannemer_vloerverwarming")
            }
            else if(idSelected === "inopdrachtvoor_ventilatieinstallatie")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming(onderaannemers_ventilatieinstallatie,ordernr_onderaannemer_ventilatieinstallatie,"onderaannemers_ventilatieinstallatie","ordernr_onderaannemer_ventilatieinstallatie")
            }
            else if(idSelected === "inopdrachtvoor_")
            {
                return renderInputsInopdrachtvoorFZVloerverwarming(onderaannemers_,ordernr_onderaannemer,"onderaannemers_","ordernr_onderaannemer")
            }
           
            
        }
    }
    const algemeneInput = () => {
        return(
            <>
                <div className="col-xxl-6">
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Project nummer</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" id="nameInput" value={project.projectnr} placeholder="Enter your name" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="projectnaam" class="form-label">Project naam</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="projectnaam" onChange={(e) => onChangeData(e)} id="projectnaam" value={projectnaam} placeholder="voorbeeld: 3 woningen Amsterdam" required/>
                                {error ===true ? formData.projectnaam.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="plaats" class="form-label">Plaats</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm" name="plaats" onChange={(e) => onChangeData(e)} id="plaats" value={plaats} placeholder="voorbeeld: Amsterdam" />
                                {error ===true ? formData.plaats.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="land" class="form-label">Land</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="land" 
                                    defaultValue={formData.land}
                                    className="form-select form-select-sm"
                                    onChange={(e) => choosedCountry(e)}
                                >
                                    <option></option>
                                    <option value="nl">Nederland</option>
                                    <option value="en">England</option> 
                                </select>
                                {error ===true ? formData.land.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="provincies" class="form-label">Provincie</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="provincies"
                                    name="provincie" 
                                    defaultValue={formData.provincie}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option></option>
                                    {
                                        
                                        pronvincies !== undefined ? pronvincies.map((item) => <option key={item} value={item}>{item}</option>): <option> - Geen resultaten - </option>
                                    }
                                </select>
                                {error ===true ? formData.provincie.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                       
                        
                    </div>
                </div>
                <div className="col-xxl-6">
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="projectstatus" class="form-label">Project status</label>
                            </div>
                            <div class="col-lg-6">
                                <select 
                                    id="projectstatus"
                                    name="projectStatus" 
                                    className="form-select form-select-sm"
                                    defaultValue={formData.projectStatus}
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option></option>
                                    {
                                        projectstatus.map((item) => <option key={item} value={item}>{item}</option>)
                                    }
                                </select>
                                {error ===true ? formData.projectStatus.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedKlanten" class="form-label">Kies opdrachtgever</label>
                            </div>
                            <div class="col-lg-6">
                                <select 
                                    id="selectedKlanten"
                                    name="selectedKlanten"
                                    defaultValue={selectedKlanten} 
                                    className="form-select form-select-sm"
                                    onBlur={(e) => returnGegevensPerklant(e.target.value)}
                                    // onBlur = {() => returnGegevensPerklant()}
                                >   <option></option>
                                    {
                                        klanten !== undefined ? klanten.map((item) => <option key={item.id} value={item.id}>{item.klantnaam}</option>): <option> - No country is choosen</option>
                                    }
                                </select>
                                {error ===true ? formData.selectedKlanten.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                            <div class="col-lg-3">
                                <button type="button" title='klant toevoegen' onClick={() => setShow(true)} className="btn btn-success btn-icon waves-effect waves-light"><i className="ri-add-fill"></i></button>
                                { show ?  <KlantModalAanmaken props={{'show':show,'setShow':setShow,'provincies':pronvincieS ? pronvincieS:[]}}/>:null}
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="offertenr" class="form-label">Offertenummer</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="offertenr" onChange={(e) => onChangeData(e)} id="offertenr" value={offertenr} placeholder="voorbeeld: GH76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="exactnr" class="form-label">Exactnummer</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="exactnr" onChange={(e) => onChangeData(e)} id="exactnr" value={exactnr} placeholder="voorbeeld: 76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="debiteurnr" class="form-label">Debiteurnummer</label>
                            </div>
                            <div class="col-lg-6">
                                <input type="text" class="form-control form-control-sm" name="debiteurnr" onChange={(e) => onChangeData(e)} id="debiteurnr" value={debiteurnr} placeholder="voorbeeld: 76453" />
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="renovatie_nieuwbouw_" class="form-label">Renovatie / Nieuwbouw</label>
                            </div>
                            <div class="col-lg-6">
                                <select 
                                    id="renovatie_nieuwbouw_" 
                                    defaultValue={renovatie_nieuwbouw_}
                                    name='renovatie_nieuwbouw_'
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option></option>
                                    <option value="Renovatie">Renovatie</option>
                                    <option value="Nieuwbouw">Nieuwbouw</option> 
                                </select>
                                {error ===true ? formData.renovatie_nieuwbouw_.trim() ? null: <span className={validationsMessages}>Veld moet worden ingevuld</span> :null}
                            </div>
                        </div>
                        
                    </div>
                    
                </div>
            </>
        )
        
    } 
    const contactpersonenAanemer = () => {
        return(
            <>
                <div className='col-xxxl-6'>
                    <div className='card card-animate'>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <button type="button" title='contact person klant aanmaken' onClick={() => handleModalklantMedewerker()} className="btn btn-success btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                                {displayModalAanemer()}   
                                </div>
                        </div>
                    </div>
                </div>
                <div className='col-xxl-6'>
                    <div className="card card-animate">
                        
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedProjecleider" class="form-label">Projectleider</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="selectedProjecleider"
                                    name='selectedProjecleiderAanermer'
                                    defaultValue={selectedProjecleiderAanermer}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option> </option>
                                    {projectleiderAanemersValues!== undefined ? 
                                     projectleiderAanemersValues.map((item) => <option key={item.id} value={item.id}>{item.name}</option>): 'Geen gegevens'}
                                </select>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedwerkvoorbereider" class="form-label">Werkvoorbereider</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="selectedwerkvoorbereider"
                                    name='selectedwerkvoorbereiderAanemer'
                                    defaultValue={selectedwerkvoorbereiderAanemer}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option> </option>
                                    {werkVoorbereidersAanemersValues !== undefined ? 
                                     werkVoorbereidersAanemersValues.map((item) => <option key={item.id} value={item.id}>{item.name}</option>): 'Geen gegevens'}
                                   
                                </select>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="selectedUivoerder" class="form-label">Uitvoerder</label>
                            </div>
                            <div class="col-lg-9">
                                <select 
                                    id="selectedUivoerder"
                                    name='selectedUivoerderAanemer'
                                    defaultValue={selectedUivoerderAanemer}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option> </option>
                                    {uitvoerdersAanemersValues !== undefined ? 
                                     uitvoerdersAanemersValues.map((item) => <option key={item.id} value={item.id}>{item.name}</option>): 'Geen gegevens'}
                                   
                                </select>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='col-xxl-6'>
                    <div className="card card-animate">
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Telefoon projectleider</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm"  id="nameInput" defaultValue={returnNumberPhone(projectleidersAanemers,selectedProjecleiderAanermer)} placeholder="" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Telefoon werkvoorbereider</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm"  id="nameInput" defaultValue={returnNumberPhone(werkvoorbereidersAanemers,selectedwerkvoorbereiderAanemer)} placeholder="" disabled/>
                            </div>
                        </div>
                        <div className='row m-3'>
                            <div class="col-lg-3">
                                <label for="nameInput" class="form-label">Telefoon uitvoerder</label>
                            </div>
                            <div class="col-lg-9">
                                <input type="text" class="form-control form-control-sm"  id="nameInput" defaultValue={returnNumberPhone(uitvoerdersAanemers,selectedUivoerderAanemer)} placeholder="" disabled/>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    const displayFormMedewerker = () => {
        return  <MedewerkerModal props={{'showModalFzMedewerker': showModalFzMedewerker,'setShowModalFzMedewerker':setShowModalFzMedewerker}} />
        
    }
    const contactpersonenFZ = () => {
        return(
        <>
            <div className="col-xxxl-6">
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <button title='contact person FZ aanmaken' type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-success  waves-effect waves-light "><i className="ri-add-fill"></i></button>
                            {displayFormMedewerker()}
                        </div> 
                    </div>
                </div>
            </div>
            <div className='col-xxl-6'>
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="selectedProjecleiderFz" class="form-label">Projectleider</label>
                        </div>
                        <div class="col-lg-6">
                            <select 
                                id="selectedProjecleiderFz"
                                name='selectedProjecleiderFz'
                                defaultValue={selectedProjecleiderFz}
                                className="form-select form-select-sm"
                                onChange={(e) => onChangeData(e)}
                            >
                                <option> - Kies option - </option>
                                {projectleiderfz !== undefined && projectleiderfz !== null? 
                                    projectleiderfz.map((item) => <option key={item.id} value={item.id}>{item.voornaam + ' ' + item.achternaam}</option>): 'Geen gegevens'}
                                
                            </select>
                        </div>
                        {/* <div className="col-lg-3">
                            <button type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-danger btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                            {displayFormMedewerker()}
                        </div> */}
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="selectedProjecleiderFz" class="form-label">Werkvoorbereider</label>
                        </div>
                        <div class="col-lg-6">
                            <select 
                                id="selectedWerkvoorbereiderFz"
                                name='selectedWerkvoorbereiderFz'
                                defaultValue={selectedWerkvoorbereiderFz}
                                className="form-select form-select-sm"
                                onChange={(e) => onChangeData(e)}
                            >
                                <option> - Kies option - </option>
                                { werkvoorbereiderfz !== undefined && werkvoorbereiderfz !== null ? 
                                    werkvoorbereiderfz.map((item) => <option key={item.id} value={item.id}>{item.voornaam + ' ' + item.achternaam}</option>): 'Geen gegevens'}
                                
                            </select>
                        </div>
                        {/* <div className="col-lg-3">
                            <button type="button" onClick={() => setShowModalFzMedewerker(true)} className="btn btn-danger btn-icon waves-effect waves-light "><i className="ri-add-fill"></i></button>
                            {displayFormMedewerker()}
                        </div> */}
                    </div>
                </div> 
            </div>
            <div className='col-xxl-6'>
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Telefoon projectleider</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm"  id="nameInput" defaultValue={ returnNumberPhone(projectleiderfz,selectedProjecleiderFz)} placeholder="" disabled/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Telefoon Werkvoorbereider</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm"  id="nameInput" defaultValue={returnNumberPhone(werkvoorbereiderfz,selectedWerkvoorbereiderFz)} placeholder="" disabled/>
                        </div>
                    </div>
                </div>
            </div>
        </>
        )
    }
    const installationComponenten = () => {
        return(
        <>
            
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Afgifte systeem</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtvoor_vloerverwarming"
                                    name='inopdrachtvoor_vloerverwarming'
                                    defaultValue={inopdrachtvoor_vloerverwarming}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {renderInstallatieComponenten(inopdrachtvoor_vloerverwarming,"inopdrachtvoor_vloerverwarming")}
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Ventilatieinstallatie</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtvoor_ventilatieinstallatie"
                                    name='inopdrachtvoor_ventilatieinstallatie'
                                    defaultValue={inopdrachtvoor_ventilatieinstallatie}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                    
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {renderInstallatieComponenten(inopdrachtvoor_ventilatieinstallatie,"inopdrachtvoor_ventilatieinstallatie")}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='col'>
                <div className="card card-animate">
                    <div className='m-3'>
                        <h5>Zonnepanelen</h5>
                        <div className='row m-3'>
                            <div class="col-lg-9">
                                <select 
                                    id="inopdrachtvoor_"
                                    name='inopdrachtvoor_'
                                    defaultValue={inopdrachtvoor_}
                                    className="form-select form-select-sm"
                                    onChange={(e) => onChangeData(e)}
                                >
                                 
                                    <option value="Derden">Derden</option>
                                    <option value="FZ">Fz</option>
                                    
                                </select>
                                {renderInstallatieComponenten(inopdrachtvoor_,"inopdrachtvoor_")}
                            </div>
                        </div>
                    </div>    
                </div>
            </div>
            

        </>
        )
    }
    const datumsenopmerkingen = () => {
        return(
        <>
            <>{submit ? <><MessagingModal props={{'showModalMessage':showModalMessage, 'setShowModalMessage':setShowModalMessage,'message': message ? props.message:'er geen melding gevonden','severity': severity.current}} /></>:null}</>
            <div className="col-xxl-6">
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Datum systeem invoer</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="text" class="form-control form-control-sm" data-provider="flatpickr"  name='system_datum_invoer' value={returnDateFormat(currentDate())}  placeholder="" disabled/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Start datum</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="startDatum" value={startDatum} onChange={e => onChangeData(e)}  placeholder="" />
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Offerte datum</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="offerteDatum" value={offerteDatum} onChange={e => onChangeData(e)} placeholder="offerteDatum"/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Uitlijk datum opdracht indien WTW</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="uitlijk_datum_opdracht_indien_wtw" value={uitlijk_datum_opdracht_indien_wtw} onChange={e => onChangeData(e)}  placeholder="Uitlijk datum opdracht indien WTW"/>
                        </div>
                    </div>
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="nameInput" class="form-label">Uitlijk datum opdracht alleen Icem</label>
                        </div>
                        <div class="col-lg-9">
                            <input type="date" class="form-control form-control-sm" data-provider="flatpickr"  name="uitlijk_datum_opdracht_alleen_icem" value={uitlijk_datum_opdracht_alleen_icem} onChange={e => onChangeData(e)}  placeholder="Uitlijk datum opdracht alleen Icem"/>
                        </div>
                    </div>
                </div>
            </div>
            
            <div className="col-xxl-6">
                <div className="card card-animate">
                    <div className='row m-3'>
                        <div class="col-lg-3">
                            <label for="textarea" className='form-label'>Opmerking</label>
                        </div>
                        <div class="col-lg-9">
                            <textarea className='form-control' id="textarea" name="opmerking" value={opmerking} onChange={e => onChangeData(e)}></textarea>
                        </div>
                    </div>
                </div>
            </div>
        </>
        )
    }

  return (
    <>
        
        <div class="page-title-box d-sm-flex align-items-center justify-content-between">
            <h4 class="mb-sm-0">Project aanmaken {props.context !== undefined ? props.context.projectnr: ''}</h4>
        </div>
        <form novalidate>
            <h5 className="mb-3">Algemene projectgegevens</h5>
            <div className="row">
                {algemeneInput()}
            </div>
            <h5 className="mb-3">Contactpersonen aanemer</h5>
            <div className="row">
            
                {contactpersonenAanemer()}
            </div>
            <h5 className="mb-3">Contactpersonen Factory zero</h5>
            <div className="row">
                {contactpersonenFZ()}
            </div>

            <h5 className="mb-3">Installations componenten</h5>
            <div className="row">
                {installationComponenten()}
            </div>
            
            <h5 className="mb-3">Datums en opmerking </h5>
            <div className="row">
                {displayModalMessages()}
                {datumsenopmerkingen()}
            </div>
            
            <div className="row mb-3">
                <div className="col-lg-6">
                    <button type="button" onClick={(e) => submitForm(e)} className="btn btn-success btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Opslaan</button>
                </div>
                <div className="col-lg-6">
                    <button type="button" onClick={(e) => submitCancel(e)} className="btn btn-warning btn-label waves-effect right waves-light rounded-pill"><i class="ri-check-double-line label-icon align-middle rounded-pill fs-16 ms-2"></i> Cancel</button>
                </div>
            </div>
            

        </form>
        
    </>
  )
}

const getProjectItems = state => ({
    message: state.project.is_success,
    context: state.project.context,
    bedrijaanemer: state.project.data
})
export default  connect(getProjectItems,{load_project,aanmaken_klant,aanmaken_project,aanmaken_klantMedewerker,medewerker_aanmaken,bedrijf_aanemer_bedrijf_aanmaken})(ProjectAanmaken)